/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";

function BreadCrumb({
  onClick = () => {},
  previousLink,
  currentLink,
  navigateLinks = [],
}) {
  return (
    <>
      <nav aria-label="breadcrumb" className="breadcrumb_nav">
        <ol className="breadcrumb">
          <li className="breadcrumb-item" onClick={onClick}>
            <a>{previousLink}</a>
          </li>
          {/* navigatable links with navigations */}
          {navigateLinks?.map((item) => {
            return (
              <li className="breadcrumb-item" onClick={item?.clickHandler}>
                <a>{item?.link}</a>
              </li>
            );
          })}

          {/* fixed link of current tab */}
          <li className="breadcrumb-item active" aria-current="page">
            {currentLink}
          </li>
        </ol>
      </nav>
    </>
  );
}

export default BreadCrumb