import { convertReactSelectData } from "components/utility";
import {
  SET_CITY_LISTING,
  SET_COUNTRY_LISTING,
  SET_BRANCHES_LISTING,
  SET_PHARMACIES_LISTING,
} from "../actions/actionType";

const initialState = {
  pharmacies: [],
  branches: [],
  countries: [],
  cities: [],
};

function filterReducer(state = initialState, action) {
  switch (action.type) {
    case SET_PHARMACIES_LISTING:
      return {
        ...state,
        pharmacies: action.payload,
      };
    case SET_BRANCHES_LISTING:
      return {
        ...state,
        branches: action.payload,
      };
    case SET_COUNTRY_LISTING:
      return {
        ...state,
        countries: action.payload,
      };
    case SET_CITY_LISTING:
      return {
        ...state,
        cities: action.payload,
      };
    default:
      return state;
  }
}

export default filterReducer;
