import "./styles.scss";
import {
  ERROR_MESSAGES,
  ERROR_TYPES_GENERATION,
  INPUT_TYPES,
  LABEL,
  PASSWORD_TYPES,
  STRINGS,
} from "./constants";
import React, { useEffect, useState } from "react";
import {
  typeSelection,
  validateConfirmPassword,
} from "components/utility";
import startsWith from 'lodash.startswith';
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import CustomButton from "../CustomButton";
import CustomTextInput from "../CustomTextInput";
import { Images } from "shared/images";
import { useForm } from "react-hook-form";
import {
  CountryDropdown,
  RegionDropdown,
} from "react-country-region-selector";
const CustomForm = ({
  onSubmit = () => {},
  renderSubmitButton = true,
  id = "hook-form",
  formData = [],
  privacyPolicy = false,
  country = "",
  region = "",
  jobAlert = false,
  phone = "",
  title = "",
  identityDocument = "",
  defaultValues = {},
}) => {
  const [phoneNumber, setPhoneNumber] = useState(phone);
  const [phoneNumberError, setPhoneNumberError] = useState(false);
  const [identityDoc, setIdentityDoc] = useState(identityDocument);
  const [countryError, setCountryError] = useState(false);
  const [regionError, setRegionError] = useState(false);
  const [countryName, setCountryName] = useState(country);
  const [dialCode, setDialCode] = useState("");
  const [regionName, setRegionName] = useState(region);
  const [typeToggle, setTypeToggle] = useState({
    password: INPUT_TYPES.PASSWORD,
    [STRINGS.CONFIRM_PASSWORD]: INPUT_TYPES.PASSWORD,
  });
  const {
    register,
    handleSubmit,
    watch,
    reset,
    formState,
    formState: { errors },
  } = useForm({ defaultValues: { ...defaultValues } });

  const policies = (registerValue, required, additionalInformation) => {
    return (
      <div className="mb-3 check_error">
        <div className="checkbox">
          <input
            type="checkbox"
            {...register(registerValue, { required: required })}
          />
          <label>{additionalInformation}</label>
        </div>
      </div>
    );
  };
  useEffect(() => {
    if (formState.isSubmitSuccessful) {
      // reCaptcha && recaptchaRef.current.reset();
      // reset({ ...defaultValues });
    }
  }, [formState, reset]);
  useEffect(() => {
    Object.keys(defaultValues).length && reset({ ...defaultValues });
  }, [defaultValues]);
  useEffect(() => {
    // Object.keys(defaultValues).length && reset({ ...defaultValues });
    setCountryName(country);
    setRegionName(region);
    setPhoneNumber(phone);
    setIdentityDoc(identityDocument);
  }, [country, identityDocument, phone, region]);
  return (
    <form
      id={id}
      onSubmit={handleSubmit((data, event) => {
        if (id === "contactus") {
          if (countryName && regionName && phoneNumber) {
            onSubmit(data, countryName, regionName, phoneNumber, dialCode);
            countryName && setCountryError(false);
            regionName && setRegionError(false);
            phoneNumber && setPhoneNumberError(false);
          }else{
            !countryName && setCountryError(true);
            !regionName && setRegionError(true);
            !phoneNumber && setPhoneNumberError(true);
          }
        } else {
          onSubmit(data, event);
        }
      })}
    >
      <div className="row">
        {formData.map((formItem, formIndex) => {
          return (
            <div
              className="col-md-6 mb-4"
              key={`${formItem.registerName} + ${formIndex}`}
            >
              {formItem.registerName === "phone_number" ? (
                <div className="country_dropdown">
                  <label>{LABEL.PHONE_NUMBER}</label>
                  <PhoneInput
                    inputProps={{
                      name: "phone",
                      // required: true,
                    }}
                    value={phoneNumber}
                    country={"gb"}
                    isValid={(inputNumber, country, countries) => {
                      return countries.some((country) => {
                        return startsWith(inputNumber, country.dialCode) || startsWith(country.dialCode, inputNumber);
                      });
                    }}
                    onChange={(data, country) => {
                      if (data?.length <= 4) {
                        setPhoneNumberError(true);
                      } else {
                        setPhoneNumberError(false);
                        setPhoneNumber(data);
                        setDialCode(country.dialCode);
                      }
                    }}
                    titleText={formItem.labelName}
                  />
                  {phoneNumberError && (
                    <span className="error">{ERROR_MESSAGES.PHONE_NUMBER}{console.log("error here")} </span>
                  )}
                </div>
              ) : formItem.registerName === "identity_documents" ? (
                <>
                  <label>{LABEL.DOCUMENTS}</label>
                  <div className="position-relative file_upload_field">
                    <div className="input_file">
                      <CustomTextInput
                        id={"identity-documents"}
                        type={INPUT_TYPES.FILE}
                        onChange={(event) => {
                          if (event.target.files[0]) {
                            setIdentityDoc(event.target.files[0]);
                            event.target.value = null;
                          }
                        }}
                        accept="application/pdf,application/vnd.ms-excel"
                      />
                      <label
                        className="data_label form-control"
                        data-label="Choose File"
                        id={"identity-documents"}
                      >
                        Choose File
                      </label>
                    </div>

                    {identityDoc && (
                      <div className="upload_doc">
                        <span>
                          {STRINGS.PDF_NAME}
                          {/* {identityDoc?.name ?? identityDoc} */}
                        </span>
                        <span
                          className="btn-danger"
                          id={"identity-documents"}
                          onClick={() => {
                            setIdentityDoc("");
                          }}
                        >
                          <svg
                            width="20"
                            height="20"
                            viewBox="0 0 20 20"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              fill-rule="evenodd"
                              clip-rule="evenodd"
                              d="M1.38109 19.389C1.65055 19.6584 1.91981 19.793 2.32402 19.793C2.72802 19.793 2.99747 19.6582 3.26695 19.389L10.4055 12.2504L17.5441 19.389C17.8136 19.6584 18.2176 19.793 18.4871 19.793C18.7565 19.793 19.1605 19.6582 19.43 19.389C19.9687 18.8503 19.9687 18.0421 19.43 17.5034L12.2914 10.3648L19.43 3.22618C19.9687 2.68746 19.9687 1.87927 19.43 1.34056C18.8913 0.801845 18.0831 0.801845 17.5444 1.34056L10.4058 8.47915L3.26719 1.34056C2.72848 0.801845 1.92029 0.801845 1.38157 1.34056C0.842861 1.87927 0.842861 2.68746 1.38157 3.22618L8.52016 10.3648L1.38157 17.5034C0.842861 18.0423 0.842861 18.8503 1.38157 19.3892L1.38109 19.389Z"
                              fill="white"
                            />
                          </svg>
                        </span>
                      </div>
                    )}
                  </div>
                </>
              ) : formItem.registerName === "country" ? (
                <div className="position-relative">
                  <label>{LABEL.COUNTRY}</label>
                  <div className="country_picker">
                    <CountryDropdown
                      value={countryName}
                      onChange={(val) => {
                        setCountryName(val);
                        if (val) {
                          setCountryError(false);
                        } else {
                          setCountryError(true);
                          setRegionName("");
                        }
                      }}
                      defaultOptionLabel={LABEL.SELECT}
                    />
                  </div>
                  {countryError && (
                    <span className="error">{ERROR_MESSAGES.COUNTRY}</span>
                  )}
                </div>
              ) : formItem.registerName === "state" ? (
                <>
                  <label>{LABEL.STATE}</label>
                  <div className="country_picker">
                    <RegionDropdown
                      country={countryName}
                      value={regionName}
                      onChange={(val) => {
                        setRegionName(val);
                        if (val) {
                          setRegionError(false);
                        } else {
                          setRegionError(true);
                        }
                      }}
                      defaultOptionLabel={LABEL.SELECT}
                      blankOptionLabel={LABEL.SELECT}
                      className="w-100"
                    />
                  </div>
                  {regionError && (
                    <span className="error">{ERROR_MESSAGES.STATE}</span>
                  )}
                </>
              ) : (
                <>
                  <CustomTextInput
                    readOnly={!!formItem?.disabled}
                    id={formItem.registerName}
                    placeholder={formItem.placeholder}
                    cursor={formItem?.cursor ? formItem?.cursor : ""}
                    accept={
                      formItem.type === "file"
                        ? "application/pdf,application/vnd.ms-excel"
                        : "*"
                    }
                    type={
                      PASSWORD_TYPES.includes(formItem.registerName)
                        ? typeToggle[formItem.registerName]
                        : formItem.type
                    }
                    icon={
                      PASSWORD_TYPES.includes(formItem.registerName)
                        ? typeToggle[formItem.registerName] ===
                          INPUT_TYPES.PASSWORD
                          ? Images.HidePassword
                          : Images.ShowPassword
                        : null
                    }
                    onIconClick={(data) => typeSelection(data, setTypeToggle)}
                    iconStyle={"formIcon"}
                    titleText={formItem.labelName}
                    {...register(formItem.registerName, {
                      pattern: formItem.pattern,
                      required: formItem.required,
                      validate: (val) =>
                        validateConfirmPassword(
                          val,
                          formItem.registerName,
                          watch
                        ),
                    })}
                  />
                  {ERROR_TYPES_GENERATION.includes(
                    errors[formItem.registerName]?.type
                  ) && (
                    <span className="error">
                      {
                        formItem.errorMessages[
                          errors[formItem.registerName]?.type
                        ]
                      }
                    </span>
                  )}
                  {/* </div> */}
                </>
              )}
            </div>
          );
        })}
        {privacyPolicy && (
          <>
            {policies(
              STRINGS.PRIVACY_POLICY_REGISTER,
              true,
              STRINGS.PRIVACY_POLICY
            )}
            {errors[STRINGS.PRIVACY_POLICY_REGISTER]?.type === "required" ? (
              <span className="error">{ERROR_MESSAGES.PRIVACY_POLICY}</span>
            ) : null}
          </>
        )}
        {jobAlert &&
          policies(STRINGS.JOB_ALERT_REGISTER, false, STRINGS.JOB_ALERT)}
        {renderSubmitButton && (
          <div className="text-center mb-4">
            <CustomButton
              className="btn btn-md btn-primary"
              type="submit"
              title={title || STRINGS.SUBMIT}
            />
          </div>
        )}
      </div>
    </form>
  );
};

export default CustomForm;
