import { Fade, Tooltip } from "@material-ui/core";
import React, { useCallback, useEffect, useState } from "react";
import { Images } from "shared/images";
import {
  BUTTON_TEXT,
  VERIFY_TOOLTIP,
  USERS_CONSTS,
  DOCUMENT_TYPE,
  SORT_KEY,
  NA,
  STRINGS,
  NOT_RATED,
} from "shared/constants";
import ButtonStatus from "components/atom/button/ButtonStatus";
import { LOCUM_USER, PHARMACY_USER } from "shared/tableData";
import DialogView from "components/atom/dialogview";
import {
  TruncateString,
  disableDocViewer,
  filterByDocumentData,
  filterByDocumentType,
  formatUserAddress,
  getLocalizedDateString,
  truncateText,
  verifyStatusSwitch,
} from "components/utility";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import VerifiedLogo from "components/atom/verifiedIcon";
import SortIcon from "components/molecules/sortIcon";
import CheckboxMultiBody from "components/molecules/checkboxMulti/body";
import CheckboxHeader from "components/molecules/checkboxMulti/header";
import Ratingstars from "components/atom/rating";
import withTooltip from "components/molecules/withTooltip";

function UserTable({
  data,
  activeTab,
  userStatusChange = () => {},
  setViewDoc = () => {},
  setDocViewerParams = () => {},
  setmultiplePayload = () => {},
  multiplepayload,
  userView,
  sortKey,
  setSortKey = () => {},
  sortType,
  setSortType = () => {},
  showNotUploadedSnack,
  setDocType = () => {},
  handleSort = () => {},
  setPageCount = () => {},
  pageCount = 1,
  itemsPerPage = 15,
}) {
  const [headerLabel, setHeaderLabel] = useState({});
  const [multipleSelect, setMultipleSelect] = useState(false);
  const [toggle, setToggle] = useState({
    isActive: false,
    name: "",
  });
  const docButton = [
    {
      type: DOCUMENT_TYPE.EDUCATION,
      image: Images.education,
      buttonText: BUTTON_TEXT.EDUCATION,
      visible: activeTab === USERS_CONSTS.LOCUM_USERS.ID,
    },
    {
      type: DOCUMENT_TYPE.RIGHT_TO_WORK,
      image: Images.license,
      buttonText: BUTTON_TEXT.RIGHT_TO_WORK,
      visible: activeTab === USERS_CONSTS.LOCUM_USERS.ID,
    },
    {
      type: DOCUMENT_TYPE.LICENSE,
      image: Images.license,
      buttonText: BUTTON_TEXT.LICENSE,
      visible: activeTab === USERS_CONSTS.PHARMACY_USERS.ID,
    },
    {
      type: DOCUMENT_TYPE.INSURANCE,
      image: Images.insurance,
      buttonText: BUTTON_TEXT.INSURANCE,
      visible: true,
    },
    {
      type: DOCUMENT_TYPE.IDENTITY,
      image: Images.identity,
      buttonText: BUTTON_TEXT.IDENTITY,
      visible: activeTab === USERS_CONSTS.LOCUM_USERS.ID,
    },
  ];

  // Change heading title for table according to tabs
  const onTabChange = useCallback(() => {
    setmultiplePayload([]);
    setPageCount(1);
    if (activeTab === USERS_CONSTS.PHARMACY_USERS.ID) {
      setHeaderLabel(PHARMACY_USER);
    } else {
      setHeaderLabel(LOCUM_USER);
    }
  }, [setHeaderLabel, activeTab]);

  useEffect(() => {
    onTabChange();
  }, [activeTab, onTabChange]);

  const sortSwitchKey = (name) => {
    switch (name) {
      case PHARMACY_USER.PHARMACY_NAME:
        return setSortKey(
          activeTab === USERS_CONSTS.PHARMACY_USERS.ID
            ? SORT_KEY.company_name
            : SORT_KEY.first_name
        );
      case LOCUM_USER.NAME:
        return setSortKey(
          activeTab === USERS_CONSTS.PHARMACY_USERS.ID
            ? SORT_KEY.company_name
            : SORT_KEY.first_name
        );
      case PHARMACY_USER.CREATED_ON:
        return setSortKey(SORT_KEY.createdAt);
      case LOCUM_USER.CREATED_ON:
        return setSortKey(SORT_KEY.first_name);
      case PHARMACY_USER.EMAIL || LOCUM_USER.EMAIL:
        return setSortKey(SORT_KEY.email);
      case LOCUM_USER.EMAIL:
        return setSortKey(SORT_KEY.email);
      default:
        return setSortKey(SORT_KEY.default);
    }
    setSortType(0);
  };

  const sortImage = (name) => {
    switch (name) {
      case PHARMACY_USER.PHARMACY_NAME:
        return true;
      case LOCUM_USER.NAME:
        return true;
      case PHARMACY_USER.CREATED_ON:
        return true;
      case PHARMACY_USER.EMAIL:
        return true;
      default:
        return false;
    }
  };

  useEffect(() => {
    if (multiplepayload?.length === 0) setMultipleSelect(false);
  }, [multiplepayload]);

  const DocStatusButton = ({
    docData,
    docType,
    id,
    image,
    buttonText,
    visible,
  }) => {
    if (visible) {
      return (
        <ButtonStatus
          image={image}
          notUploaded={!disableDocViewer(docData, docType)}
          status={verifyStatusSwitch(filterByDocumentType(docData, docType))}
          title={buttonText}
          onClick={() => {
            if (disableDocViewer(docData, docType)) {
              setViewDoc(true);
              setDocViewerParams([
                id,
                docType,
                filterByDocumentType(docData, docType),
                filterByDocumentData(docData, docType),
              ]);
            } else {
              showNotUploadedSnack();
            }
          }}
        />
      );
    } else {
      return null;
    }
  };

  return (
    <>
      <thead>
        <tr key={1000 + 1}>
          <th>
            <CheckboxHeader
              multipleSelect={multipleSelect}
              setmultiplePayload={setmultiplePayload}
              setMultipleSelect={setMultipleSelect}
              data={data}
              multiplepayload={multiplepayload}
            />
          </th>
          {Object.values(headerLabel).map((i, idx) => (
            <th scope="col" id={idx}>
              {i}
              {sortImage(i) ? (
                <SortIcon
                  toggleIcon={
                    toggle.name === i && toggle.isActive ? true : false
                  }
                  onClick={() => {
                    if (toggle.name !== i) setSortType(0);
                    setToggle({
                      name: i,
                      isActive: !toggle.isActive,
                    });
                    sortSwitchKey(i);
                    setPageCount(1);
                    handleSort();
                  }}
                />
              ) : null}
              {i === PHARMACY_USER.VERIFY_STATUS ? (
                <span className="position-relative">
                  <Tooltip
                    title={
                      <React.Fragment>
                        <DialogView>
                          {VERIFY_TOOLTIP.map((i, index) => {
                            return (
                              <div key={index} className="dropDown-list">
                                <em className="me-2">
                                  <img src={i.IMG} alt="" />
                                </em>
                                <span>{i.TITLE}</span>
                              </div>
                            );
                          })}
                        </DialogView>
                      </React.Fragment>
                    }
                    arrow={true}
                    placement={"bottom"}
                    enterDelay={200}
                    TransitionComponent={Fade}
                    TransitionProps={{ timeout: 300 }}
                    followCursor={true}
                  >
                    <em className="ms-2">
                      <img src={Images.infoIcon} alt="info" />
                    </em>
                  </Tooltip>
                </span>
              ) : null}
            </th>
          ))}
        </tr>
      </thead>
      {data?.length ? (
        data.map((value, index) => (
          <tbody key={index}>
            <tr>
              <td width={70}>
                <CheckboxMultiBody
                  setMultipleSelect={setMultipleSelect}
                  setmultiplePayload={setmultiplePayload}
                  value={value}
                  multiplepayload={multiplepayload}
                  data={data}
                />
              </td>
              <td>
                <span>{value?.id ? value?.id : NA} </span>
              </td>
              {activeTab === USERS_CONSTS.PHARMACY_USERS.ID ? (
                <>
                  <td
                    onClick={() => userView(value.id)}
                    style={{ cursor: "pointer" }}
                  >
                    {withTooltip({
                      string: value?.profile_data?.company_name,
                      truncate: true,
                      truncateLength: 15,
                      isVerified: value?.profile_data.verified,
                    })}
                  </td>
                  <td>
                    {withTooltip({
                      string: value?.first_name + " " + value?.last_name,
                      truncate: true,
                      truncateLength: 10,
                    })}
                  </td>
                </>
              ) : (
                <>
                  <td
                    onClick={() => userView(value.id)}
                    style={{ cursor: "pointer" }}
                  >
                    {withTooltip({
                      string: value?.first_name + " " + value?.last_name,
                      truncate: true,
                      truncateLength: 10,
                      isVerified: parseInt(value?.verified_document) === 100,
                    })}
                  </td>
                </>
              )}

              <td>
                {withTooltip({
                  string: value?.email,
                  truncate: true,
                  truncateLength: 35,
                  isVerified: value?.profile_data?.verified,
                })}
              </td>
              <td>
                {withTooltip({
                  string: formatUserAddress(value),
                  truncate: true,
                  truncateLength: 30,
                })}
              </td>

              <td>
                {value?.rating ? (
                  <div className="rating">
                    <Ratingstars rating={value?.rating || NOT_RATED} />
                  </div>
                ) : (
                  NOT_RATED
                )}
                {/* <span>
                <Ratingstars rating={value?.rating || 0} />
                </span> */}
              </td>
              <td>{getLocalizedDateString(value?.profile_data?.created_at)}</td>

              <td>
                <ActivateDeactivate
                  statusChange={userStatusChange}
                  value={value}
                />
              </td>
              <td>
                <div className="d-flex">
                  {docButton.map((i) => {
                    return (
                      <DocStatusButton
                        docData={value.document_data}
                        docType={i.type}
                        id={value?.id}
                        image={i.image}
                        buttonText={i.buttonText}
                        visible={i.visible}
                      />
                    );
                  })}
                </div>
              </td>
              <td>
                <ButtonStatus
                  image={Images.viewIcon}
                  status={"default"}
                  title={BUTTON_TEXT.VIEW}
                  onClick={() => userView(value.id)}
                />
              </td>
            </tr>
          </tbody>
        ))
      ) : (
        <tbody>
          <tr>
            <td
              colSpan={Object.values(headerLabel).length + 1}
              className="text-center py-5"
            >
              {STRINGS.NO_DATA_AVAILIABLE}
            </td>
          </tr>
        </tbody>
      )}
    </>
  );
}

export default UserTable;
