import { Fade, Tooltip } from "@material-ui/core";
import VerifiedLogo from "components/atom/verifiedIcon";
import { TruncateString } from "components/utility";
import React from "react";
import { NA } from "shared/constants";

function withTooltip({
  string = "",
  truncate = false,
  truncateLength = 15,
  isVerified = false,
}) {
  return (
    <div>
      <Tooltip
        title={string}
        arrow={true}
        placement={"bottom"}
        enterDelay={200}
        TransitionComponent={Fade}
        TransitionProps={{ timeout: 300 }}
        followCursor={true}
      >
        {JSON.stringify(string)?.length ? (
          <span>
            {truncate
              ? TruncateString(string, truncateLength, truncateLength)
              : string}
            {isVerified ? <VerifiedLogo isVerified={isVerified} /> : null}
          </span>
        ) : (
          NA
        )}
      </Tooltip>
    </div>
  );
}

export default withTooltip;
