//libs
import React from "react";
import { CountryDropdown, RegionDropdown } from "react-country-region-selector";
//consts
import { Images } from "shared/images";
import {
  FILTERS_ENUM,
  FILTER_TYPES,
  LABEL,
  STRINGS,
  docStatusUpdate,
} from "./constants";
//styles
import "./style.scss";
import CustomButton from "../CustomButton";
import DateRangeSelector from "components/atom/dateRange";
import RangeSlider from "../RangeSlider";
import {
  PHARMACY_DETAILS,
  PHARMACY_USER_VIEW,
  USERS_CONSTS,
  VACANCY_TYPE,
} from "shared/constants";
import {
  statusUpdate,
  vacancyStatus,
} from "components/atom/searchBar/filterConsts";
import ReactSelect from "components/atom/ReactSelect";
import { useDispatch, useSelector } from "react-redux";
import { branchFilterListing } from "redux/actions/auth";

function FiltersCustom({
  filterData = {},
  toggle = false,
  handleChangeFilterData = () => {},
  reset = () => {},
  handleFetchDataForFilter = () => {},
  type = VACANCY_TYPE.DASHBOARD,
  listType = VACANCY_TYPE.DASHBOARD
}) {
  const dispatch = useDispatch();
  const pharmaciesList = useSelector((state) => state.filters.pharmacies);
  const branchesList = useSelector((state) => state.filters.branches);
  const countryList = useSelector((state) => state.filters.countries);
  const cityList = useSelector((state) => state.filters.cities);
  const filterButtonsDashboard = [
    {
      title: "Activation Status",
      type: FILTER_TYPES.DROPDOWN,
      data: statusUpdate,
      id: FILTERS_ENUM.blocked,
      value: filterData?.activation,
      handleChange: (value) => {
        handleChangeFilterData("activation", value);
      },
    },
    {
      title: "Document Status",
      type: FILTER_TYPES.DROPDOWN,
      data: docStatusUpdate,
      id: FILTERS_ENUM.blocked,
      value: filterData?.document,
      handleChange: (value) => {
        handleChangeFilterData("document", value);
      },
    },
  ];

  const locationFilters = () => {
    return (
      <>
        <div className="col-lg-3 col-md-6 col-sm-6 mb-3 position-relative">
          <label>{LABEL.COUNTRY}</label>
          <div className="country_picker ">
            <CountryDropdown
              value={filterData["country"]}
              whitelist={
                VACANCY_TYPE.LOCUM_USER == listType
                  ? ""
                  : [
                      "GB",
                      "ENG",
                      "SCT",
                      "WLS",
                      "GB-ENG",
                      "GB-SCT",
                      "GB-WLS",
                      "IE",
                    ]
              }
              onChange={(val) => {
                handleChangeFilterData("state", "");
                handleChangeFilterData("country", val);
              }}
              defaultOptionLabel={LABEL.SELECT}
            />
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-6 mb-3 position-relative">
          <label>{LABEL.STATE}</label>
          <div className="country_picker">
            <RegionDropdown
              country={filterData["country"]}
              value={filterData["state"]}
              onChange={(val) => {
                handleChangeFilterData("state", val);
              }}
              defaultOptionLabel={LABEL.SELECT}
              blankOptionLabel={LABEL.SELECT}
            />
          </div>
        </div>
      </>
    );
  };

  const dateFilter = () => {
    return (
      <div className="col-lg-3 col-md-6 col-sm-6  mb-3">
        <DateRangeSelector
          dateRange={{
            startDate: filterData["selectedDate"].from || null,
            endDate: filterData["selectedDate"].to || null,
            key: "selection",
          }}
          setDateRange={(val) =>
            handleChangeFilterData("selectedDate", {
              from: val.startDate,
              to: val.endDate,
            })
          }
          titleText={LABEL.START_DATE}
          icon={Images.calendar}
        />
      </div>
    );
  };

  const statusFilters = () => {
    return (
      <>
        {filterButtonsDashboard?.map((i, idx) => {
          return (
            <div className="col-lg-3 col-md-6 col-sm-6  mb-3">
              <label className="d-block text-capitalize">{i.title}</label>
              <div className="size-md  ">
                <ReactSelect
                  options={i.data}
                  handleChange={i.handleChange}
                  selectedOption={i?.value}
                  title={"Select"}
                  classNamePrefix="react-prefix"
                  className="react-select"
                />
              </div>
            </div>
          );
        })}
      </>
    );
  };

  const pharmacyFilters = () => {
    return (
      <div className="col-lg-3 col-md-6 col-sm-6 mb-3">
        <label className="d-block text-capitalize">
          {PHARMACY_USER_VIEW.PHARMACY_USERS}
        </label>
        <div className="size-md 125">
          <ReactSelect
            options={pharmaciesList}
            handleChange={(value) => {
              handleChangeFilterData("branch", "");
              handleChangeFilterData("pharmacy", value);
              dispatch(branchFilterListing({ pharmacy_id: value.value }));
            }}
            selectedOption={filterData?.pharmacy}
            title={"Select"}
            classNamePrefix="react-prefix"
            className="react-select"
            //menuIsOpen={true}
          />
        </div>
      </div>
    );
  };

  const branchFilters = () => {
    return (
      <div className="col-lg-3 col-md-6 col-sm-6 mb-3">
        <label className="d-block text-capitalize">
          {PHARMACY_USER_VIEW.BRANCHES}
        </label>
        <div className="size-md ">
          <ReactSelect
            options={branchesList}
            isDisabled={!filterData?.pharmacy}
            handleChange={(value) => {
              handleChangeFilterData("branch", value);
            }}
            selectedOption={filterData?.branch}
            title={"Select"}
            classNamePrefix="react-prefix"
            className="react-select"
          />
        </div>
      </div>
    );
  };

  const vacancyStatusFilters = () => {
    return (
      <div className="col-lg-3 col-md-6 col-sm-6 mb-3">
        <label className="d-block text-capitalize">{"Status"}</label>
        <div className="size-md ">
          <ReactSelect
            options={vacancyStatus}
            handleChange={(value) => {
              handleChangeFilterData("status", value);
            }}
            selectedOption={filterData?.status}
            title={"Select"}
            classNamePrefix="react-prefix"
            className="react-select"
          />
        </div>
      </div>
    );
  };

  const rangefilter = () => {
    return (
      <div className="col-lg-3 col-md-6 col-sm-6 mb-3">
        <RangeSlider
          state={filterData["rateRange"]}
          changeHandler={(val) => handleChangeFilterData("rateRange", val)}
          titleText={STRINGS.RATE}
        />
      </div>
    );
  };

  const filterSwitch = () => {
    switch (type) {
      case VACANCY_TYPE.USER:
        return (
          <>
            {pharmacyFilters()}
            {branchFilters()}
            {locationFilters()}
            {statusFilters()}
          </>
        );
      case VACANCY_TYPE.LOCUM_USER:
        return (
          <>
            {locationFilters()}
            {statusFilters()}
          </>
        );
      case VACANCY_TYPE.PHARMACY_USER:
        return (
          <>
            {locationFilters()}
            {statusFilters()}
          </>
        );
      case VACANCY_TYPE.DASHBOARD:
        return (
          <>
            {pharmacyFilters()}
            {branchFilters()}
            {locationFilters()}
            {dateFilter()}
            {rangefilter()}
            {vacancyStatusFilters()}
          </>
        );
      case VACANCY_TYPE.PHARMACY:
        return (
          <>
            {branchFilters()}
            {locationFilters()}
            {dateFilter()}
            {rangefilter()}
            {vacancyStatusFilters()}
          </>
        );
      case VACANCY_TYPE.BRANCHES:
        return (
          <>
            {dateFilter()}
            {rangefilter()}
            {vacancyStatusFilters()}
          </>
        );
      case VACANCY_TYPE.INVOICES:
        return <>{dateFilter()}</>;

      default:
        break;
    }
  };

  return (
    <>
      {toggle ? (
        <div className={`card filters_col mt-3 ${!toggle ? "d-none" : ""}`}>
          <div className="card-body bg-white">
            <div className="row">
              {filterSwitch()}
              <div className="col-2 mb-3">
                <label className="d-none d-md-block">&nbsp;</label>
                <div className="btn_grp">
                  <CustomButton
                    Icon={Images.reset}
                    className="btn btn-sm btn-primary "
                    onClick={reset}
                  />
                  <CustomButton
                    Icon={Images.whiteTick}
                    className="btn btn-sm btn-secondary ms-2"
                    onClick={handleFetchDataForFilter}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : null}
    </>
  );
}

export default FiltersCustom;
